import { gql } from '@apollo/client';
export const GET_FARMS = gql`
  query Query(
    $page: Int
    $offset: Int
    $type: String
    $name: String
    $platformName: String
  ) {
    allFarms(
      page: $page
      offset: $offset
      type: $type
      name: $name
      platformName: $platformName
    ) {
      data {
        id
        chainId {
          brokerId
          id
          name
          abbrev
          apiKey
          walletAddress
          contractAddress
          enabled
        }
        platformName
        poolTitle
        poolId
        beefyVaultAddress
        token0 {
          price
          icon
          contractAddress
          chainId {
            brokerId
            id
            name
            abbrev
            apiKey
            walletAddress
            contractAddress
            enabled
          }
        }
        token1 {
          price
          icon
          contractAddress
          chainId {
            brokerId
            id
            name
            abbrev
            apiKey
            walletAddress
            contractAddress
            enabled
          }
        }
        type
        apy
        price
        riskNumber
        deleted
        enabled
      }
      count
      total
      current
      code
      message
    }
  }
`;

export const SYNC_FARMS = gql`
  query Query {
    syncFarms {
      code
      message
      data {
        id
        platformName
        poolTitle
        poolId
        beefyVaultAddress
        type
        apy
        price
        deleted
        enabled
      }
    }
  }
`;
export const ENABLE_DISABLE_FARM = gql`
  mutation Mutation($farmId: ID, $enabled: Boolean) {
    enableDisableFarm(farmId: $farmId, enabled: $enabled) {
      code
      message
      data {
        platformName
        poolTitle
        poolId
        beefyVaultAddress
        type
        apy
        price
        deleted
        enabled
      }
    }
  }
`;

