import React, { useState, useCallback, useEffect } from 'react';
import DualAssetsList from './DualAssetsList';
import { useParams } from 'react-router-dom';
import { success, error } from '../../../shared/Helpers';
import DragnDrop from '../../../shared/DragnDrop';
import {
  _SUCCESS,
  _ERROR,
  _ASC,
  _DESC,
  _ROLE,
  _NAME,
  _NEW,
  _STAKES,
  ADMINS,
  _NO_CACHE,
  OFFSET
} from '../../../../utils/Constants';
import {
  ENABLE_DISABLE_FARM,
  SYNC_FARMS,
  GET_FARMS,
  ASSIGN_RISK_NUMBER
} from '../../../../graphql/farms';
import { useQuery, useMutation } from '@apollo/client';
import { Button } from '@mui/material';
import FormModal from '../../../shared/Modals/FormModal';
import FilterForm from './Filter/FilterForm';

import FloatingButton from '../../../shared/Button-float';

import { parseResponse } from '../../../../utils';

function PoolManagement() {
  const [isOpen, setIsOpen] = useState(false);
  const [actionState, setActionState] = useState(false);
  const [chains, setChains] = useState(null);
  const [page, setPage] = useState(1);
  const [data, setData] = useState([]);
  const [totalPages, setTotalPages] = useState(0);
  const [filterBy, setFilterBy] = useState('initial');
  const [filterObj, setFilterObj] = useState(null);
  const [fil, setFil] = useState({ page: page, offset: OFFSET });
  // const [fil, setFil] = useState({ page: page, offset: OFFSET });

  const { loading, error, refetch, data: fData } = useQuery(GET_FARMS, {
    variables: fil
  });

  const {
    loading: sync_loading,
    error: sync_error,
    refetch: syncFarms,
    data: sync_data
  } = useQuery(SYNC_FARMS, {
    // fetchPolicy: 'cache-first'
    // pollInterval: 20000
  });

  const [enableDisableFarm] = useMutation(ENABLE_DISABLE_FARM);
  const [assignRiskNumber] = useMutation(ASSIGN_RISK_NUMBER);

  useEffect(() => {
    (async () => {
      const res = await refetch(fil);

      if (fil) {
        const resp = await parseResponse(res, 'allFarms');
        setData(resp && resp);
      }
    })();
  }, [fil]);

  useEffect(() => {
    if (fData) {
      (async () => {
        const resp = await parseResponse(fData, 'allFarms');
        setData(resp && resp);
      })();
    }
  }, [fData]);

  useEffect(() => {
    if (filterObj) {
      let obj = {};
      if (filterObj.name && filterObj.name !== '') {
        obj = {
          ...obj,
          name: filterObj.name
        };
      }
      if (filterObj.platformName && filterObj.platformName !== '') {
        obj = {
          ...obj,
          platformName: filterObj.platformName
        };
      }
      obj = { ...obj, page: page, offset: OFFSET };
      setFil(obj);
    }
  }, [filterObj]);

  const openFilterModal = useCallback(() => {
    setFilterBy('open');
    setIsOpen(true);
  }, []);

  const closeFilterModal = useCallback(() => {
    setFilterBy('initial');
    setIsOpen(false);
  }, []);

  const onResetFilterModal = useCallback(() => {
    setFil({ page: page, offset: OFFSET });
    setFilterObj({ page: page, offset: OFFSET });
  }, []);

  const setFilterObject = useCallback(obj => {
    setFilterObj(obj);
  }, []);

  const openModal = useCallback(() => {
    setActionState(false);
    setChains(null);
    setIsOpen(true);
  }, []);

  const onHandleEnableChange = async (enabled, id) => {
    const response = await enableDisableFarm({
      variables: { farmId: id, enabled: enabled }
    });

    refetch(fil);

    let resp;
    if (response) {
      resp = await parseResponse(response, 'enableDisableFarm');
    }

    if (resp && resp.code === 200) {
      setTimeout(success(resp.message), 6000);
    } else {
      error(resp.message);
    }
  };

  const onHandleAssignRisk = async (id, riskNumber) => {
    const response = await assignRiskNumber({
      variables: { id: id, riskNumber: riskNumber }
    });

    // refetch(fil);

    let resp;
    if (response) {
      resp = await parseResponse(response, 'assignRiskNumber');
    }

    if (resp && resp.code === 200) {
      success(resp.message);
    } else {
      error(resp.message);
    }
  };

  const refreshDualAssetsList = () => {
    syncFarms();
    refetch(fil);
  };

  const onHandleFilter = async (values, handles) => {
    setFilterObject(values);
  };

  return (
    <div className={'h-full overflow-auto'}>
      <DualAssetsList
        data={data}
        page={page}
        setPage={setPage}
        loading={loading}
        refreshDualAssetsList={refreshDualAssetsList}
        onHandleEnableChange={onHandleEnableChange}
        filterBy={filterBy}
        disable={isOpen}
        actionState={actionState}
        setActionState={setActionState}
        onHandleAssignRisk={onHandleAssignRisk}
      />
    </div>
  );
}

export default PoolManagement;
