import React from 'react';
import { TextField } from '@mui/material';
import { SMALL, TEXT } from '../../Constants';

function InputTextField({
  label,
  value,
  onChange,
  name,
  error,
  helperText,
  type,
  disabled,
  format = 'dd/MM/yyyy',
  placeholder,
  shrink,
  InputProps,
  inputProps
}) {
  return (
    <TextField
      fullWidth
      type={type || TEXT}
      className="custom-text-field"
      label={label}
      value={value}
      // defaultValue={type === 'date' ? value : ' '}
      onChange={onChange}
      placeholder={placeholder || ''}
      size={SMALL}
      name={name}
      error={error}
      helperText={helperText}
      disabled={disabled || false}
      format={type === 'date' ? format : ''}
      InputLabelProps={
        shrink
          ? {
              shrink: shrink
            }
          : null
      }
      InputProps={InputProps ? InputProps : null}
      // InputProps={inputProps ? inputProps : null}
    />
  );
}

export default InputTextField;
