import React, { useState, useEffect } from 'react';
import { Autocomplete, TextField, Paper } from '@mui/material';

export default function AutoComplete(props) {
  const { id, title, options, className, valueName, handleSortByChain } = props;
  const [value, setValue] = useState({});

  return (
    <div className="w-full">
      <Autocomplete
        id={id}
        className={className}
        options={options}
        getOptionLabel={option => option.name}
        PaperComponent={({ children }) => <Paper>{children}</Paper>}
        // value={value}
        onChange={(event, value) => {
          handleSortByChain(value && value.id ? value.id : '', id);
          setValue(value);
        }}
        renderInput={params => (
          <TextField
            {...params}
            variant="outlined"
            size="small"
            fullWidth
            label={title}
            id="group"
            name="group"
            type="text"
            value={value}
          />
        )}
      />
    </div>
  );
}
