import React, { useState, useEffect, useCallback } from 'react';
import { headCells, setAdminArr, filteredData } from './helper';

import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';

import Tooltip from '@mui/material/Tooltip';
import Section from './shared/MultiSection/Section';
import FormModal from '../../../shared/Modals/FormModal';
import RiskForm from './AssignRisk/RiskForm';

import TrippleSection from '../../../shared/TrippleSection';
import CustomButton from '../../../shared/Buttons/index';
import AutoComplete from '../../../shared/AutoComplete/index';

import { useHistory } from 'react-router-dom';
import {
  TableLoader,
  EmptyTableRow,
  FarmSectionLoader
} from '../../../shared/Tables';
import { _NAME, STAKES } from '../../../../utils/Constants';
import { Chains } from '../../../../config/config';
import DialogBox from '../../../shared/DialogBox';

import {
  ASC,
  DESC,
  ROWS_PER_PAGE,
  ROWS_PER_PAGE_OPTIONS,
  CENTER,
  RIGHT,
  EDIT,
  _ACTIVE,
  _INACTIVE,
  _TOP,
  SMALL
} from '../../../shared/Constants';

import ComponentHeader from '../../../shared/ComponentHeader';
import InputCheckbox from '../../../shared/Checkboxes/Checkbox';
import { parseResponse, copyToClipboard } from '../../../../utils';
import { DataNotFound } from '../../../../utils/DataNotFound';
import { useMutation } from '@apollo/client';

function DualAssetsList({
  data,
  loading,
  refreshDualAssetsList,
  onHandleEnableChange,
  onHandleAssignRisk,
  actionState,
  setActionState
}) {
  const [dualAssets, setDualAssets] = useState([]);
  const [tempDualAssets, setTempDualAssets] = useState([]);
  const [activeBasket, setActiveBasket] = useState([]);
  const [disabledBasket, setDisabledBasket] = useState([]);

  const [order, setOrder] = useState(ASC);
  const [isOpen, setOpen] = useState(false);
  const [orderBy, setOrderBy] = useState('platformName');
  const [rowItem, setRowItem] = useState(null);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(ROWS_PER_PAGE);
  const [dialog, setDialog] = useState({ visible: false });

  const history = useHistory();

  useEffect(() => {
    if (data && data.data && data.data.length > 0) {
      setDualAssets(data.data);
      setTempDualAssets(data.data);
      setPage(data.current);
    } else {
      setDualAssets([]);
      setTempDualAssets([]);
      setPage(1);
    }
  }, [data]);

  const handleCloseRiskModal = useCallback(() => {
    setOpen(false);
  }, []);

  const handleOpenRiskModal = useCallback(item => {
    setOpen(true);
    setRowItem(item);
    setActionState(item && item.riskNumber !== '' ? false : true);
  }, []);

  const handleSubmitRiskForm = useCallback(async (values, handles) => {
    const resp = await onHandleAssignRisk(values.id, values.riskNumber);
    handleCloseRiskModal();
    handles.resetForm();
  }, []);

  const handleUpdateStatusConfirmation = (status, id) => {
    setDialog({
      id: id,
      visible: true,
      header: status ? 'Unsuspend Farm?' : 'Suspend Farm?',
      message: 'Are you sure you want to do this?',
      key: Math.random()
        .toString(36)
        .substring(6),
      ok: () => onHandleEnableChange(status, id)
    });
  };

  const handleSortByChain = useCallback((name, id) => {
    if (name && name !== '' && id === 'sortByChainsActive') {
      setActiveBasket(
        dualAssets.filter(v => v.chainId.name === name && v.enabled)
      );
    } else if (name && name !== '' && id === 'sortByChainsDisabled') {
      setDisabledBasket(
        dualAssets.filter(v => v.chainId.name === name && !v.enabled)
      );
    } else {
      if (id === 'sortByChainsActive') {
        setActiveBasket(
          dualAssets &&
            dualAssets.length > 0 &&
            dualAssets.filter(da => da.enabled)
        );
      }

      if (id === 'sortByChainsDisabled') {
        setDisabledBasket(
          dualAssets &&
            dualAssets.length > 0 &&
            dualAssets.filter(da => !da.enabled)
        );
      }
    }
  });

  return (
    <div className={'h-full overflow-auto'}>
      <DndProvider backend={HTML5Backend} className={'h-full overflow-auto'}>
        <TrippleSection
          childrens={[
            <Section
              loading={loading}
              id={'newPoolsFromCryptstake'}
              data={dualAssets}
              title={'New from Cryptstake'}
              activeBasket={activeBasket}
              disabledBasket={disabledBasket}
              setActiveBasket={setActiveBasket}
              setDisabledBasket={setDisabledBasket}
              onHandleEnableChange={onHandleEnableChange}
              isOpen={isOpen}
              actionState={actionState}
              handleOpenRiskModal={handleOpenRiskModal}
              handleCloseRiskModal={handleCloseRiskModal}
              handleSubmitRiskForm={handleSubmitRiskForm}
              Child={
                <CustomButton
                  value={'Refresh'}
                  className={'btn-primary autocomplete-padding'}
                  type={'button'}
                  onClick={refreshDualAssetsList}
                  disabled={false}
                />
              }
            />,
            <Section
              id={'activePools'}
              data={dualAssets}
              title={'Active Pools'}
              activeBasket={activeBasket}
              disabledBasket={disabledBasket}
              setActiveBasket={setActiveBasket}
              setDisabledBasket={setDisabledBasket}
              onHandleEnableChange={onHandleEnableChange}
              isOpen={isOpen}
              actionState={actionState}
              loading={loading}
              handleSubmitRiskForm={handleSubmitRiskForm}
              Child={
                <AutoComplete
                  id={'sortByChainsActive'}
                  title={'Sort by Chain'}
                  options={Chains}
                  className={
                    'autocomplete-padding custom-input-select w-25 custom-text-field w-full group-by-auto-complete'
                  }
                  handleSortByChain={handleSortByChain}
                />
              }
              handleOpenRiskModal={handleOpenRiskModal}
              handleCloseRiskModal={handleCloseRiskModal}
            />,
            <Section
              id={'disabledPools'}
              data={dualAssets}
              title={'Disabled Pools'}
              btnText={'Refresh'}
              activeBasket={activeBasket}
              disabledBasket={disabledBasket}
              setActiveBasket={setActiveBasket}
              setDisabledBasket={setDisabledBasket}
              onHandleEnableChange={onHandleEnableChange}
              isOpen={isOpen}
              actionState={actionState}
              loading={loading}
              onHandleAssignRisk={onHandleAssignRisk}
              handleSubmitRiskForm={handleSubmitRiskForm}
              Child={
                <AutoComplete
                  id={'sortByChainsDisabled'}
                  title={'Sort by Chain'}
                  options={Chains}
                  className={
                    'autocomplete-padding custom-input-select w-25 custom-text-field w-full group-by-auto-complete'
                  }
                  handleSortByChain={handleSortByChain}
                />
              }
              handleOpenRiskModal={handleOpenRiskModal}
              handleCloseRiskModal={handleCloseRiskModal}
            />
          ]}
        />

        <FormModal
          maxWidth="xs"
          open={isOpen}
          onClose={handleCloseRiskModal}
          title={'Set Risk Number'}>
          <RiskForm
            rowItem={rowItem}
            setRowItem={setRowItem}
            onHandleSubmit={handleSubmitRiskForm}
            loading={loading}
            onClose={handleCloseRiskModal}
            actionState={actionState}
            handleOpenRiskModal={handleOpenRiskModal}
          />
        </FormModal>
      </DndProvider>
    </div>
  );
}

export default DualAssetsList;
