import React, { useEffect } from 'react';
import Grid from '@mui/material/Grid';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import Button from '@mui/material/Button';
import { TextField as MaterialTextField } from '@mui/material';
import Autocomplete from '@mui/material/Autocomplete';
import Paper from '@mui/material/Paper';

import TextField from '../../../../shared/Inputs/TextField';
import { ASSIGN_RISK, _ASSIGN_RISK, _ID } from './constants';

const RiskFormBody = ({
  loading,
  touched,
  values,
  errors,
  handleChange,
  handleSubmit,
  setFieldValue,
  resetForm,
  actionState,
  rowItem
}) => {
  useEffect(() => {
    if (rowItem) {
      setFieldValue(_ASSIGN_RISK, rowItem.riskNumber ? rowItem.riskNumber : '');
      setFieldValue('id', rowItem.id ? rowItem.id : '');
    }
  }, [rowItem, setFieldValue]);

  return (
    <form onSubmit={handleSubmit} autoComplete="off">
      <DialogContent dividers>
        <Grid container>
          <Grid item xs={12} sm={12} md={12} lg={12} className="p-2">
            <TextField
              type="number"
              // label={ASSIGN_RISK}
              value={values.riskNumber}
              onChange={event => {
                setFieldValue(
                  _ASSIGN_RISK,
                  event.target.value ? event.target.value : ''
                );
                setFieldValue(_ID, rowItem.id ? rowItem.id : '');
              }}
              name={_ASSIGN_RISK}
              error={touched.riskNumber && Boolean(errors.riskNumber)}
              helperText={touched.riskNumber && errors.riskNumber}
            />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions className="modal-box-footer">
        <Button type="submit" className="filter-filter-btn">
          {actionState ? 'Confirm' : 'Update'}
        </Button>
      </DialogActions>
    </form>
  );
};

export default RiskFormBody;
