import * as Yup from 'yup';

const regexMnemonic = /^[A-Za-z][a-zA-Z0-9- ]+$/i;

export const initialValues = {
  name: '',
  platformName: '',
  id: ''
};

export const UserSchema = Yup.object().shape({
  platformName: Yup.string()
    .min('2', 'Min 2 characters required!')
    .matches(
      regexMnemonic,
      'Must start with an Alphabet and Alphabets, Hyphens and Numbers are allowed!'
    )
});
