import React from 'react';
import { Formik } from 'formik';
import RiskFormBody from './RiskFormBody';
import { initialValues, RiskSchema } from './helper';

const RiskForm = ({
  loading,
  onHandleSubmit,
  onClose,
  actionState,
  handleOpenRiskModal,
  rowItem
}) => {
  return (
    <Formik
      initialValues={initialValues}
      onSubmit={(values, handles) => {
        onHandleSubmit(values, handles);
      }}
      validationSchema={RiskSchema}>
      {props => {
        const {
          touched,
          values,
          errors,
          handleChange,
          handleSubmit,
          setFieldValue,
          resetForm
        } = props;
        return (
          <RiskFormBody
            onClose={onClose}
            loading={loading}
            values={values}
            handleChange={handleChange}
            handleSubmit={handleSubmit}
            setFieldValue={setFieldValue}
            resetForm={resetForm}
            errors={errors}
            touched={touched}
            actionState={actionState}
            handleOpenRiskModal={handleOpenRiskModal}
            rowItem={rowItem}
          />
        );
      }}
    </Formik>
  );
};

export default RiskForm;
