import React, { useState, useEffect, useCallback } from 'react';
import { useDrop } from 'react-dnd';
import CustomButton from '../../../../../shared/Buttons/index';
import AssetDetailsLogo from '../../../../../shared/AssetLogo';
import { Chains } from '../../../../../../config/config';
import { MovingCard } from '../MovingCard';

import { FarmSectionLoader } from '../../../../../shared/Tables';

export default function Section(props) {
  const {
    loading,
    id,
    data,
    title,
    activeBasket,
    disabledBasket,
    setDisabledBasket,
    setActiveBasket,
    onHandleEnableChange,
    isOpen,
    actionState,
    onHandleAssignRisk,
    handleOpenRiskModal,
    handleCloseRiskModal,
    handleSubmitRiskForm,
    Child
  } = props;

  const [value, setValue] = useState({});

  useEffect(() => {
    setActiveBasket(data.filter(da => da.enabled));
    setDisabledBasket(data.filter(da => !da.enabled));
  }, [data, id]);

  const handleBasket = useCallback(
    (id, item) => {
      setActiveBasket(basket => {
        if (id === 'activePools' && !basket.includes(item)) {
          const resp = onHandleEnableChange(true, item.id);
          if (resp && resp.code === 200) {
            setDisabledBasket(basket => {
              return basket.includes(item)
                ? basket.filter(v => v !== item)
                : basket;
            });
            return !basket.includes(item) ? [...basket, item] : basket;
          }
        }
        return basket;
      });

      setDisabledBasket(basket => {
        if (id === 'disabledPools' && !basket.includes(item)) {
          const resp = onHandleEnableChange(false, item.id);
          if (resp && resp.code === 200) {
            setActiveBasket(basket => {
              return basket.includes(item)
                ? basket.filter(v => v !== item)
                : basket;
            });
            return !basket.includes(item) ? [...basket, item] : basket;
          }
        }
        return basket;
      });
    },
    [id]
  );

  const handleSortByChain = useCallback(
    (value, id) => {
      if (value.id && value.id !== '' && id === 'activePools') {
        setActiveBasket(activeBasket.filter(v => v.chainId.name === value.id));
      } else if (value.id && value.id !== '' && id === 'disabledPools') {
        setDisabledBasket(
          disabledBasket.filter(v => v.chainId.name === value.id)
        );
      } else {
        setActiveBasket(data.filter(da => da.enabled));
        setDisabledBasket(data.filter(da => !da.enabled));
      }
    },
    [id]
  );

  const [{ isOver }, dropRef] = useDrop({
    accept: 'row',
    drop: item => {
      handleBasket(id, item);
    },
    collect: monitor => ({
      isOver: monitor.isOver()
    })
  });

  return (
    <div id={id} className={'section-card'}>
      <div className={'section-card-header'}>
        <div className={'section-card-header-title '}>{title}</div>
        <div className={'section-card-header-child w-25'}>{Child}</div>
      </div>
      {loading ? (
        <FarmSectionLoader className={'flex justify-center items-center'} />
      ) : (
        <div
          className={'section-card-body'}
          ref={
            id === 'activePools'
              ? dropRef
              : id === 'disabledPools'
              ? dropRef
              : {}
          }>
          {id === 'newPoolsFromCryptstake' ? (
            data && data.length > 0 ? (
              data.map(row => {
                return (
                  <MovingCard
                    row={row}
                    Chains={Chains}
                    id={id}
                    onHandleEnableChange={onHandleEnableChange}
                    isOpen={isOpen}
                    actionState={actionState}
                    handleSubmitRiskForm={handleSubmitRiskForm}
                    handleOpenRiskModal={handleOpenRiskModal}
                    handleCloseRiskModal={handleCloseRiskModal}
                  />
                );
              })
            ) : (
              <div className={'no-farm-data'}>Pools not found</div>
            )
          ) : id === 'activePools' ? (
            activeBasket && activeBasket.length > 0 ? (
              activeBasket.map(row => {
                return (
                  <MovingCard
                    row={row}
                    Chains={Chains}
                    id={id}
                    onHandleEnableChange={onHandleEnableChange}
                    isOpen={isOpen}
                    actionState={actionState}
                    handleSubmitRiskForm={handleSubmitRiskForm}
                    handleOpenRiskModal={handleOpenRiskModal}
                    handleCloseRiskModal={handleCloseRiskModal}
                  />
                );
              })
            ) : (
              data &&
              data.length <= 0 && (
                <div className={'no-farm-data'}>Pools not found</div>
              )
            )
          ) : disabledBasket && disabledBasket.length > 0 ? (
            disabledBasket.map(row => {
              return (
                <MovingCard
                  row={row}
                  Chains={Chains}
                  id={id}
                  onHandleEnableChange={onHandleEnableChange}
                  isOpen={isOpen}
                  actionState={actionState}
                  handleSubmitRiskForm={handleSubmitRiskForm}
                  handleOpenRiskModal={handleOpenRiskModal}
                  handleCloseRiskModal={handleCloseRiskModal}
                />
              );
            })
          ) : (
            data &&
            data.length <= 0 && (
              <div className={'no-farm-data'}>Pools not found</div>
            )
          )}
          {!isOver &&
          id !== 'newPoolsFromCryptstake' &&
          data &&
          data.length > 0 ? (
            <div className={'drag-card '}>Drag Pool Here</div>
          ) : (
            id !== 'newPoolsFromCryptstake' &&
            data &&
            data.length > 0 && (
              <div className={'drag-card-over'}>Drag Pool Here</div>
            )
          )}
        </div>
      )}
    </div>
  );
}
