import React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';

const DialogBox = props => {
  const [open, setOpen] = React.useState(false);
  React.useEffect(() => {
    setOpen(props.visible);
  }, [props.visible, props.message, props.header]);
  const handleClose = () => {
    setOpen(false);
  };
  return (
    <div>
      <Dialog
        id="dialog-component"
        open={open}
        onClose={handleClose}
        classes={{ paper: 'shadow-sm-dark rounded-sm dialoge-cont' }}>
        <div className="text-center p-6" id="dbox">
          <h4 className="font-weight-bold mt-4 dialoge-heading">
            {props.header}
          </h4>
          <p className="mb-0 text-black-50 dialoge-message">{props.message}</p>
          <div className="pt-1">
            <Button
              onClick={handleClose}
              className="dialoge-cancel-btn"
              id="dbox-cancel">
              <span className="btn-wrapper--label">No</span>
            </Button>
            <Button
              onClick={() => {
                props.ok();
                handleClose();
              }}
              className="dialoge-confirm-btn"
              id="dbox-ok">
              <span className="btn-wrapper--label">Yes</span>
            </Button>
          </div>
        </div>
      </Dialog>
    </div>
  );
};
export default DialogBox;
