import React from 'react';
import clsx from 'clsx';
import { SiSellfy } from 'react-icons/si';
import { GrProductHunt } from 'react-icons/gr';
import { FaCopyright } from 'react-icons/fa';
import _ from 'lodash';

const NameOnlyDetails = props => {
  const { row } = props;

  return (
    <div className={row.type === 'dual' ? 'ml-3' : 'ml-2'}>
      <span className="asset-name-span items-center">
        {_.toUpper(row.name)}
      </span>
    </div>
  );
};

const AssetsDetailsLogo = props => {
  const { row, chain, iconClass } = props;

  return (
    <>
      <div className="align-box-row">
        <div className=" mr-2">
          {row && chain ? (
            <div className={'asset-logo-avatar'}>
              <img src={chain.icon} alt="" className="w-full" />
            </div>
          ) : row && row.type === 'single' ? (
            <div className={'asset-logo-avatar'}>
              <img src={row.token0.icon} alt="" className="w-full" />
            </div>
          ) : row && row.type === 'dual' ? (
            <div className="asset-logo-avatar relative">
              <img
                className="w-5 h-5 absolute top-2 left-2"
                src={row && row.token0.icon}
                alt={'token-0'}
              />
              <img
                className="w-5 h-5 absolute bottom-2 right-2"
                src={row && row.token1.icon}
                alt={'token-1'}
              />
            </div>
          ) : (
            <NameOnlyDetails row={row} />
          )}
        </div>
      </div>
    </>
  );
};

export default AssetsDetailsLogo;
