import React from 'react';
import { useDrag } from 'react-dnd';
import CustomButton from '../../../../../shared/Buttons/index';
import AssetDetailsLogo from '../../../../../shared/AssetLogo';
import { Chains } from '../../../../../../config/config';
import FormModal from '../../../../../shared/Modals/FormModal';
import RiskForm from '../../AssignRisk/RiskForm';
import { Tooltip, Grid } from '@mui/material';

export const MovingCard = ({
  loading,
  row,
  Chains,
  id,
  ref,
  className = '',
  onHandleEnableChange,
  isOpen,
  actionState,
  handleSubmitRiskForm,
  handleOpenRiskModal,
  handleCloseRiskModal
}) => {
  const [{ isDragging }, dragRef] = useDrag({
    type: 'row',
    item: row,
    collect: monitor => ({
      isDragging: monitor.isDragging()
    })
  });
  return (
    <Grid
      container
      key={row.id}
      className={'moving-card ' + className}
      ref={dragRef}>
      <Grid xs={4} sm={4} md={4} lg={4} className="flex justify-center">
        <div className={'moving-card-child-1'}>
          <div className={'moving-card-farm-icon'}>
            <AssetDetailsLogo row={row} />
          </div>
          <div
            className={
              'moving-card-farm-text ' + (row.type === 'dual' ? 'ml-2' : 'ml-1')
            }>
            {row.poolTitle}
          </div>
        </div>
      </Grid>
      <Grid xs={4} sm={4} md={4} lg={4} className="flex justify-flex-end">
        <div className={'moving-card-child-2'}>
          <div className={'moving-card-chain-icon'}>
            <AssetDetailsLogo
              row={row}
              chain={Chains.find(v => v.id === row.chainId.name)}
            />
          </div>
          <div className={'moving-card-chain-text'}>{row.chainId.name}</div>
        </div>
      </Grid>
      <Grid xs={4} sm={4} md={4} lg={4} className="flex justify-center">
        <div className={'moving-card-child-3'}>
          <div className={'moving-card-button'}>
            {row && row.riskNumber !== '' ? (
              <Tooltip title={row.riskNumber}>
                <div className={'show-risk-number'}>{row.riskNumber}</div>
              </Tooltip>
            ) : (
              <CustomButton
                value={'Assign Risk #'}
                className={'btn-primary'}
                type={'button'}
                onClick={() => handleOpenRiskModal(row)}
                disabled={false}
              />
            )}
          </div>
        </div>
      </Grid>
    </Grid>
  );
};
